import {SET_PLAY_STATUS} from 'constants/action-types.js';
import {STOPPED} from 'constants/play-statuses.js';

export const playback = (state = {playStatus: STOPPED}, action) => {
    let newState;
    switch (action.type) {
    case SET_PLAY_STATUS:
        return {
            ...state,
            playStatus: action.payload
        };
    default:
        return state;
    }
}
 
