import React, { useEffect, useCallback, useState} from 'react';
import CircularProgress from 'components/progressbar/Circular.js';
import {connect} from 'react-redux';
import {useInterval} from 'components/hooks/useInterval.js';

const LoadingRemix = (props) => { 
    let [progress, setProgress] = useState(0);

    const callback = useCallback(() => {
        setProgress((progress + 1) % 100);
    }, [setProgress, progress]);

    useInterval(callback);
    return (<CircularProgress percentage={progress} radius={30}/>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LoadingRemix);
