import React, {useCallback} from 'react';
import {newZpore} from 'zpore/zpore.js';
import {writeScreenshot} from './screenshot.js';
import {cropAndStore} from 'utils/cropAndStore.js';
import { useAccount } from 'wagmi';

export const usePostRemix = ({
    songId,
    mintPrice,
    bpm,
    detune,
    stemsData,
    screenshot,
    caption,
    buffer,
    signer,
    start,
    setIsWaitingForSignature,
    setDownloading,
    length,
    setDownload,
    stop,
    setTransaction,
    zorbId
}) => {

    const {data: account, isError, isLoading} = useAccount();

    const post = useCallback(async () => {
        stop();
        setDownloading(true);
        let dataToStore = {caption};
        /**
           1. Crop and store on ipfs
           2. Store caption on ipfs
         */

        // todo: send to server to backup on s3
        let remixURI = await cropAndStore(buffer, start, length);

        let imageURI = "none"; //await writeScreenshot(screenshot);

        console.log('songId for remix=', songId);

        setDownload(remixURI);
        setDownloading(false);

        setIsWaitingForSignature(true);

        // given the stems data we need to generate a list of contract addresses and ids
        // i.e. the stems contract and the stemIds
        // we dont need caption no more
        let tx = await newZpore(signer, account.address, songId, remixURI, stemsData, zorbId, bpm, detune, mintPrice);
        //let transactionHash = tx.hash;
        let transactionHash = "fuck";
        setTransaction(transactionHash);
        setIsWaitingForSignature(false);
        //await tx.wait(1);
    }, [
        stemsData,
        account,
        screenshot,
        caption,
        signer,
        start,
        mintPrice,
        setIsWaitingForSignature,
        bpm,
        detune,
        songId,
        setDownload,
        setDownloading,
        length,
        setDownload
    ]);

    return post;
};
 
