import React, {useRef, useEffect, useState, useCallback} from 'react';
import {computeStemsData, fetchStemIds} from 'stems/getStems.js';

export const useRecordingListener = () => {
    let [screenshot, setScreenshot] = useState();
    let [buffer, setBuffer] = useState();
    let [waveform, setWaveform] = useState();
    let [silence, setSilence] = useState();
    let [startStep, setStartStep] = useState();
    let loopsDataRef = useRef({});
    let [loopsData, setLoopsData] = useState();
    let [stemsData, setStemsData] = useState();
    let [startTime, setStartTime] = useState();
    let [endTime, setEndTime] = useState();
    let [beatGuide, setBeatGuide] = useState();

    useEffect(() => {
        if (!loopsData || !loopsData.drums || !loopsData.synths) {
            return;
        }

        fetchStemIds(loopsData).then(x => {
            setStemsData(x);
            /*
            let computed = computeStemsData(loopsData, x);
            // todo: need to computeStemsData based on the "start-step" set by the cropper
            setStemsData(computed);
            */
        });
    }, [loopsData]);

    useEffect(() => {
        window.addEventListener("message", onMessage);
        return () => window.removeEventListener("message", onMessage);
    }, [loopsData, setWaveform, setEndTime, setBuffer, setScreenshot, setLoopsData, setStartTime, setBeatGuide]);
    
    const onMessage = useCallback((e) => {
        if (e.data.type === "drum-loops-data") {
            let data = {
                ... loopsDataRef.current,
                drums: e.data.body
            };
            loopsDataRef.current = data;
            setLoopsData(data);
        } else if (e.data.type === "synth-loops-data") {
            let data = {
                ... loopsDataRef.current,
                synths: e.data.body
            };
            loopsDataRef.current = data;
            setLoopsData(data);
        }
        if (e.data.type === "zpore-screenshot") {
            setScreenshot(e.data.body);
        }
        if (e.data.type === "slice-audio-buffer") {
            setBuffer(e.data.body);
        }
        if (e.data.type === "slice-silence") {
            setSilence(e.data.body);
        }
        if (e.data.type === "slice-start-step") {
            setStartStep(e.data.body);
        }
        if (e.data.type === "slice-audio-waveform") {
            setWaveform(e.data.body);
        }
        if (e.data.type === "beat-guide") {
            setBeatGuide(e.data.body);
        }
        if (e.data.type === "end-time") {
            setEndTime(e.data.body);
        }
    }, [loopsData, setEndTime, setWaveform, setScreenshot, setBuffer, setStartStep, setLoopsData, setBeatGuide]);

    const onCancel = useCallback(() => {
        setBuffer(undefined);
        setWaveform(undefined);
    }, [setBuffer, setWaveform]);

    return {
        buffer,
        screenshot,
        silence,
        startStep,
        stemsData,
        onCancel,
        beatGuide,
        startTime,
        waveform,
        loopsData,
        endTime
    } ;

};
