/**
 * Handles fetching of sample metadata and the actual audio
 * buffer, either from the centralized server or IPFS.
 * The plan is to eventually migrate fully to IPFS
*/
class Sample  {
    constructor(ipfsHash) {
        this.ipfsHash = ipfsHash;
        this.stemTypes = [];
    }
    
    getId() {
        return this.ipfsHash;
    }
}
export default Sample;
