import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {TYPE_TO_NAME} from 'constants/stem-types.js';

const StemTypes = ({sample}) => { 
    let [types, setTypes] = useState(sample.stemTypes);

    const toggle = useCallback((num) => {
        num = parseInt(num);
        let _types = [... types];
        if (types.includes(num)) {
            _types = types.filter(x => x !== num);
        } else {
            _types.push(num);
        }
        sample.stemTypes = _types;
        setTypes(_types);
    }, [types, setTypes]);

    return (
        <div className="stem-types">
        {Object.keys(TYPE_TO_NAME).map(
            typeNumber =>
            <div
              onClick={() => toggle(typeNumber)}
              className={types.includes(parseInt(typeNumber)) ? "stem-type selected": "stem-type"}>
              {TYPE_TO_NAME[typeNumber]}
            </div>)}
            
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(StemTypes);
