import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import Colors from 'components/Colors.js';
import './traits.scss';

const stc = require('string-to-color');

const colors = new Colors();


const color = (t) => {
    return colors.saturate(stc(t), 4, 1, 50);
};

const StemTraits = ({traits}) => { 
    return (
        <div className="stem-traits">
          {traits
           .filter(x => x.trait_type.includes("Stem"))
           .map(({trait_type, value})=>
            <div
              style={{
                  //color: colors.getBrightness(stc(trait_type)) < 150 ? "white" : "black",
                  borderColor: color(trait_type)}}
              className="stem-trait">
                <div className="value">{value}</div>
              </div>)}
          {traits
           .filter(x => x.trait_type.includes("bpm"))
           .map(({trait_type, value})=>
            <div
              style={{
                  //color: colors.getBrightness(stc(trait_type)) < 150 ? "white" : "black",
                  borderColor: color(trait_type)}}
              className="stem-trait">
                <div className="type">{trait_type}</div>
              <div className="value">{roundBPM(value)}</div>
              </div>)}
        </div>);
};

const roundBPM = (bpm) => {
    bpm = parseFloat(bpm);
    return Math.round(bpm * 10) / 10;
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(StemTraits);
