import {
    side,
    SW,
    SE,
    NE,
    NW,
    N,
    E,
    W,
    S,
    go,
    corner} from './paths.js';

export const peval = ({radius, algo, relative, width, height}) => {
    // starting point based on radius
    let d = `M 0, ${radius} `;

    let s = undefined;

    let depth=0;

    // keep track of how much we've increased the bounds
    let xIncrease = 0;
    let yIncrease = 0;

    for (let sideAlgo of algo) {
        let attr = getAttributes(sideAlgo, radius, relative);
        let {_radius, _relative} = attr;
        sideAlgo = sideAlgo.filter(x => x.type !== "attribute");
        let _sideAlgo = flattenAlgo(sideAlgo);
        let total = 0;
        if (!s) {
            s = side(height, width, radius, S);
        }

        for (let step of _sideAlgo) {
            let value = _relative ? relativeValue(step.value, depth, width, height) : step.value;
            if (step.type === "walk") {
                d += s.go(value);
            } else {
                total += value[1];
                d += s.smooth(... value);
            }
        }
        d += s.complete();
        s = s.next();

        if (total < 0) {
            if (depth % 2 === 0) {
                // increase x
                xIncrease += -total;
            } else {
                yIncrease += -total;
            }
        }
        depth++;
    }
    return {xIncrease, yIncrease, d};
};

const relativeValue = (value, depth, width, height) => {
    let _a = depth % 2 ===0 ? height : width;
    let _b = depth % 2 ===0 ? width : height;
    if (Array.isArray(value)) {
        return [
            _a*value[0]/100,
            _b*value[1]/100,
            _a*value[2]/100,
        ];
    } else {
        return _a*value/100;
    }
}


const getRadius = (side) => {
    if (!side) {
        return undefined;
    }
    let attr = side.filter(x => x.type==="attribute")
        .find(x => x.value.type === "radius");
    if (attr) {
        return attr.value.value;
    }
    return undefined;
};


const getAttributes = (sideAlgo, radius, relative) => {
    let _attributes = sideAlgo.filter(x => x.type === "attributes");
    let _radius = radius;
    let _relative = relative;
    for (let attr of _attributes) {
        if (attr.type === "radius") {
            _radius = attr.value;
        }
        if (attr.type === "relative") {
            _relative = attr.value;
        }
    }
    return {radius: _radius, relative: _relative};
};

const flattenAlgo = (sideAlgo) => {
    let _sideAlgo = [];
    for (let x of sideAlgo) {
        if (x.operations) {
            _sideAlgo = [... _sideAlgo, ...x.operations];
        } else {
            _sideAlgo.push(x);
        }
    }
    return _sideAlgo;
};
