import React, { useRef, useEffect, useCallback, useState} from 'react';
import ZorbOption from './ZorbOption.js';
import { ConnectButton, lightTheme} from '@rainbow-me/rainbowkit';
import {connect} from 'react-redux';
import {useTokenOwners} from 'utils/zorbs.js';
import './zorb.scss';

/**
 * Zorb Selector should only be here after it loaded
 */
const ZorbSelector = ({account, setZorbId, zorbId, setZorbStartPosition, playerRef}) => { 
    let ref = useRef();
    let {zorbs} = useTokenOwners({account})

    let [loaded, setLoaded] = useState(true);

    useEffect(() => {
        window.addEventListener("message", onMessage);
    }, [setLoaded, setZorbId]);

    useEffect(() => {
        if (zorbId && loaded && playerRef) {
            let image = zorbs.find(x => parseInt(x.tokenId) == zorbId).image.url;
            playerRef.current.contentWindow.postMessage({
                type: "zorb-id",
                body: image
            }, "*");
        }
    }, [zorbId, loaded, zorbs, playerRef]);

    const onMessage = useCallback((e) => {
        if (e.data.type === "lootbox-item-selected") {
            setZorbId(parseInt(e.data.body));
        }
    }, [setLoaded, setZorbId]);

    useEffect(() => {
        if (loaded && zorbs && playerRef && playerRef.current) {
            let loot = {};
            for (let zorb of zorbs) {
                loot[zorb.tokenId] = zorb.image.url;
            }
            console.log("sending lootbox=", loot);
            playerRef.current.contentWindow.postMessage({
                type: "lootbox",
                body: loot
            }, "*");
        }
    }, [zorbs, playerRef, loaded]);

    useEffect(() => {
        if (zorbs[0]) {
            //setZorbId(parseInt(zorbs[0].tokenId));
        }
    }, [zorbs]);

    let [hide, setHide] = useState(false);
    useEffect(() => {
        if (zorbId !== undefined) {
            setTimeout(() => {
                setHide(true);
            }, 1000);
        }
    }, [zorbId]);
    if (hide) {
        return '';
    }
    return (<div
              ref={ref}
              onClick={() => console.log("zs clicked")}
              className="zs-container">
              <div
                onClick={() => console.log("zorb selector clicked")}
                className="zorb-selector">
              {!zorbId && <p>
                            insert a zorb to power the spore and remix sound in its own way
                          </p>}
              {zorbs.length === 0 && account ? <div className="no-zorbs">No zorbs detected. </div> : zorbs.length === 0 &&
               <div className="connect-button-wrapper zorb-connect">
                 <ConnectButton
                   accountStatus="address" 
                   showBalance={{ smallScreen: false, largeScreen: false }}
                   label="Connect Wallet"
                 />
               </div>}

            {/*<div onClick={() => console.log("zorb list clicked")} className="zorbs-list">
                {zorbs.map(
                    x => <ZorbOption containerRef={ref} setZorbStartPosition={setZorbStartPosition} zorbId={zorbId} setZorbId={setZorbId} zorb={x}/>
                )}
                </div>*/}
            </div>
            </div>);
}

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZorbSelector);
