import ENSName from 'components/ens/ENSName.js';
import ZporeControlHeader from './header/ZporeControlHeader.js';
import {getSongTitle} from 'stems/getStems.js';
import {setPlayStatus} from 'actions/index.js';
import {useZporePlaybackListener} from 'components/hooks/useZporePlaybackListener.js';
import { ConnectButton, lightTheme} from '@rainbow-me/rainbowkit';
import ZporeLoading from './ZporeLoading.js';
import ZorbRemixes from './remixes/ZorbRemixes.js';
import ZorbAnimation from './zorbs/ZorbAnimation.js';
import {useFlags} from 'flags/flags.js';
import SliceRecorder from './saving/SliceRecorder.js';
import {useZporeHide} from './useZporeHideUI.js';
import ZorbSelector from './zorbs/ZorbSelector.js';
import { useAccount } from 'wagmi';
import React, { useEffect, useRef,useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './zpore.scss';
import Drop from '../drops/Drop.js';
import '../drops/thugger.scss';
import '../drops/nextgen.scss';

//const CID = "QmWEEvyNZmr3kngUjE8TNRRC5FmnXZLC1TdQk87UYe1cQL";
const AMPLIFY = "https://main.djdhx85izle40.amplifyapp.com/?gallery=true";
//const AMPLIFY = "https://zequencer.mypinata.cloud/ipfs/QmULQvsCWhHqwpjsPF4SjqVynt6bab2mC93P8hPF3N8nti/";

const Zpore = (props) => { 
    useFlags();

    useZporePlaybackListener(props);

    let [remixes, setRemixes] = useState([]);
    let [artist, setArtist] = useState(props.artist);
    let [title, setTitle] = useState(props.songTitle);
    let [showRemixes, setShowRemixes] = useState(props.showRemixes);
    let [finished, setFinished] = useState(false);
    let [isSlicing, setIsSlicing] = useState(false);
    let [entered, setEntered] = useState(false);
    let [remove, setRemove] = useState(props.showRemixes);
    let [zorbId, setZorbId] = useState(props.showRemixes ? 1 : undefined);
    let [insertedZorb, setInsertedZorb] = useState();
    let [zorbStartPosition, setZorbStartPosition] = useState(undefined);
    const hideUI = false; //useZporeHide();
    const {data: account, isError, isLoading} = useAccount();
    const [started, setStarted] = useState(false);

    useEffect(() => {
        if (props.songId) {
            getSongTitle(props.songId).then(
                ({title, artist}) => {
                    setTitle(title);
                    setArtist(artist);
                });
        }
    }, [props.songId]);

    const clicks = useRef(0);

    useEffect(() => {
        window.addEventListener("click", () => clicks.current++);
    }, []);
    useEffect(() => {
        if (account && finished && clicks.current > 0) {
            setEntered(true);
        }
    }, [account, setEntered, finished]);

    useEffect(() => {
        console.log("playStatus changed=", props.playStatus);
        if (props.playStatus) {
            setStarted(true);
        }
    }, [props.playStatus, setStarted]);

    useEffect(() => {
        if (zorbId !== undefined) {
            setTimeout(() => {
                setInsertedZorb(zorbId);
            }, 1000);
        }
    }, [zorbId, setInsertedZorb]);

    useEffect(() => {
        if (entered) {
            setTimeout(() => {
                setRemove(true);
            }, 1000);
        }
    }, [entered, setRemove]);

    let className = "next-gen zpore";
    if (account) {
        className += " connected";
    }
    if (hideUI) {
        className += " hide-ui";
    }
    if (isSlicing) {
        className += " is-slicing";
    }

    if (showRemixes) {
        className += " showing-remixes";
    }
    return (
        <div onClick={() => console.log("root clicked")} className={className}>
          {zorbId && <ZorbRemixes songId={props.songId} remixes={remixes} show={showRemixes} setShow={setShowRemixes}/>}
          {started && <SliceRecorder songId={props.songId} setIsSlicing={setIsSlicing} zorbId={zorbId}/>}
          {entered && <ZorbSelector
             account={account}
             setZorbId={setZorbId}
             zorbId={zorbId}
             setZorbStartPosition={setZorbStartPosition}/>}
          {entered && zorbStartPosition && <ZorbAnimation setZorbStartPosition={setZorbStartPosition} zorbStartPosition={zorbStartPosition}/>}
          {!remove &&
           <div className={!entered ? "enter-overlay" : "enter-overlay hide"}>
             <div
                          className="inner-overlay">
                          <img
                            className=""
                            src="/spore-logo-future4.png"/>
                          {!finished && <ZporeLoading setFinished={setFinished}/>}
                          {!finished ?
                           <div className="click-text">
                            <div>zorb + spore = zpores</div>
                            <div>
                              load your zorb into the spore for unique remixing powers
                            </div>
            </div> : <div className="click-text">
                       {account ? <button
                                    className="enter-btn"
                                    onClick={() => setEntered(true)}
                                  ><div>welcome back, <ENSName address={account.address}/></div></button> : <ConnectButton
              accountStatus="address" 
              showBalance={{ smallScreen: false, largeScreen: false }}
              label="Connect to Enter"
                                 />}
            </div>
           }
             </div>
           </div> 
          }
          <ZporeControlHeader
            remixes={remixes}
            setRemixes={setRemixes}
            setShowRemixes={setShowRemixes}
            showRemixes={showRemixes}
            songId={props.songId} artist={artist} title={title}/>
          <Drop
            zorbId={insertedZorb}
            hideWaveform={true}
            color="black"
            url={props.songId !== undefined ? AMPLIFY+"&songId=" + props.songId : AMPLIFY}
            collectionAddress={undefined} //'0x7269c1A1b8aBA1390770193771F92bD722c6a77B'
            isFullScreen={true}
            simpleControls={true}
            tokenId={1}
            className="thugger"
            //artist={artist || "Keyon Christ"}
            isSimple={true}>
          </Drop>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef,
      playStatus: state.playback.playStatus
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setPlayStatus: x => dispatch(setPlayStatus(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Zpore);
