import AudioUnit from 'engine/audio.js';
import React, {useRef, useCallback, useEffect, useState} from 'react';
import {decodeAudioData} from 'utils/decode.js';
import Tone from 'tone';
import ipfs from 'decentralized/Ipfs.js';

const bufferToArrayBuffer = require('buffer-to-arraybuffer');

const getSound = async (hash) => {
    if (hash.startsWith("http")) {
        console.log('fetching hash=', hash);
        let resp = await fetch(hash);
        return await resp.blob();
    }
    return await ipfs.getSound(hash, 10000000);
};
export const useAudioPlayer = (hash, autoPlay, isLooping) => {
    let [audioPlayer, setAudioPlayer] = useState();
    let currentHash = useRef();
    let [loaded, setLoaded] = useState();
    let [loading, setLoading] = useState(false);
    let playerRef = useRef();

    const dispose = useCallback(() => {
        if (audioPlayer) {
            audioPlayer.dispose();
        }
    }, [audioPlayer]);

    useEffect(() => {
        if (!hash) {
            if (playerRef.current) {
                playerRef.current.stopSound();
                //setAudioPlayer(undefined);
            }
            return;
        }
        if (playerRef.current) {
            playerRef.current.stopSound();
            //setAudioPlayer(undefined);
        }

        if (currentHash.current === hash && playerRef.current) {
            playerRef.current.playSound(0);
            return;
        }


        setLoading(true);
        setLoaded(false);
        getSound(hash).then(
            async blob => {
                let arrayBuffer = blob.arrayBuffer ? await blob.arrayBuffer() : bufferToArrayBuffer(blob);
                let audioBuffer = await decodeAudioData(arrayBuffer, hash);
                let player = undefined;
                if (playerRef.current) {
                    player = playerRef.current;
                    player.stopSound();
                } else {
                    player = new AudioUnit(Tone.Master.context);
                }
                player.setBuffer(audioBuffer);
                setAudioPlayer(player);
                playerRef.current = player;
                setLoaded(true);
                setLoading(false);
                if (autoPlay) {
                    player.playSound(0, 100000, isLooping);
                }
                currentHash.current = hash;
            });
    }, [hash, setAudioPlayer, setLoaded, setLoading]);

    return {
        loaded,
        loading,
        setLoaded, 
        audioPlayer,
        dispose
    };
};
