import React, { useEffect, useRef, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import SliceWaveform from './SliceWaveform.js';
import {useRecordingListener} from './useRecordingListener.js';
import './recorder.scss';

/**
 * When the user clicks the "red" "finished recording" button, this view
 * is shown. It is the top-level component of the entire record -> mint flow

 1. Receives all the recording data from the iframe [this component handles it]
 2. Shows waveform allowing cropping the recording to taste (SliceWaveform)
 3. Shows a "caption" input field for writing text to go w/ the recording
 4. Crops the screenshot of the generative art (and uploads to IPFS)
 5. Triggers a transaction for minting the "sliced recording" + caption + art as an NFT
 */

const SliceRecorder = ({playerRef, zorbId, setIsSlicing, songId}) => { 
    let {
        buffer,
        screenshot,
        silence,
        beatGuide,
        startTime,
        startStep,
        endTime,
        stemsData,
        loopsData,
        onCancel,
        waveform} = useRecordingListener();

    useEffect(() => {
        if (buffer) {
            setIsSlicing(true);
        }
    }, [buffer, setIsSlicing]);

    const onClick = useCallback(() => {
        // tell the iframe "we want the last 30 sec of audio recorded"
        // the hook above (useRecordingListener), will wait for a return message
        // with the recording buffer
        playerRef.current.contentWindow.postMessage({
            type: "slice-audio",
            body: ""
        }, "*");
    }, [playerRef]);

    return [
        <div className="slice-recorder">
          <img src="/sd-card-save.svg"
               onClick={onClick}
               className="pushable slice-recorder">
          </img>
          <div className="rec-dot"></div>
        </div>
        ,
        waveform ? <SliceWaveform
                     zorbId={zorbId}
                     screenshot={screenshot}
                     songId={songId}
                     silence={silence}
                     startStep={startStep}
                     beatGuide={beatGuide}
                     loopsData={loopsData}
                     endTime={endTime}
                     stemsData={stemsData}
                     onCancel={() => {
                         onCancel();
                         setIsSlicing(false);
                     }}
                     buffer={buffer} waveform={waveform}/> : ''
    ];
};

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SliceRecorder);
