export const SW = 'SW';
export const SE  = 'SE';
export const NE  = 'NE';
export const NW  = 'NW';
export const N = 'N';
export const S = 'S';
export const W = 'W';
export const E = 'E';

export const go = (dir, len) => {
    switch (dir) {
    case N:
        return ` c 0 -5 0 -5 0 -${len}`;
    case S:
        return ` c 0 5 0 5 0 ${len}`;
    case E:
        return ` c 5 0 5 0 ${len} 0`;
    case W:
        return ` c -5 0 -5 0 -${len} 0`;
    }
};

export const corner  = (side, size=50) => {
    switch (side) {
    case SW:
        return ` c 0, ${size}, ${size}, ${size} ${size}, ${size} `;
    case SE:
        return ` c ${size}, 0 ${size}, -${size} ${size}, -${size} `;
    case NE:
        return ` c 0, -${size} -${size}, -${size} -${size}, -${size} `;
    case NW:
        return ` c -${size}, 0 -${size}, ${size} -${size}, ${size} `;
    }
};

//export const smoothstep = (dirX, dirY, 

export const side = (sideLength, nextLength, radius, dir, depth=0, size={sideLength,nextLength}) => {
    const _go = (y1) => {
        if (y1 > sideLength) {
            return "";
        }
        sideLength -= y1;
        return go(dir, y1);
    };


    const smooth = (a1, b1, a2, b2=20) => {
        if (a2 > sideLength) {
            return _go(a2-sideLength);
        }
        sideLength -= Math.abs(a2);
        nextLength -= b1;
        switch (dir) {
        case S:
            return ` c 0,${a1} ${b1},${b2} ${b1},${a2} `;
        case E:
            return ` c ${a1},0 ${b2},${-b1} ${a2},${-b1} `;
        case N:
            return ` c 0,${-a1} ${-b1},${-b2} ${-b1},${-a2} `;
        case W:
            return ` c ${-a1},0 ${-b2},${-b1} ${-a2},${b1} `;
        }
    };

    const complete = () => {
        let _corner = dir === S ? corner(SW, radius) :
            dir === N ? corner(NE, radius) : dir === E ? corner(SE, radius) : corner(NW, radius);
        return go(dir, sideLength) + ' ' + _corner;
    };

    const next = (_radius=radius) => {
        let _nextLength = nextLength;
        return side(
            _nextLength, size.sideLength, _radius,
            dir === S ? E : dir === E ? N : dir === W ? S : W,
            depth+1,
            {
                sideLength: size.nextLength,
                nextLength: size.sideLength
            }
        );
   };
    
    return {go: _go, smooth, complete, next};
};
