import React, {useEffect, useState, useCallback} from 'react';
export const SECONDS_PER_STEP = (bpm) => {
    // how many steps in a minute?
    
    // there are 8 steps per beat so multiply by 8
    let steps_per_min = bpm*8;

    // there are 60 seconds per minute so multiply by 60
    let steps_per_second = steps_per_min / 60;

    // invert

    return 1/steps_per_second;
};

export const getRuler = ({buffer, start, pts, silence, width, bpm, startStep, length}) => {
    let [beats, setBeats] = useState([]);
    let [offset, setOffset] = useState();
    let [beatWidth, setBeatWidth] = useState();

    useEffect(() => {
        let duration = buffer[0].length/44100;

        // how many steps does this make?
        
        let ratioOffset = silence  / duration;
        let offset = ratioOffset*width*2;

        // figure out how many beats to divide by assuming 90 beats per minute

        // how many minutes
        let durationMin = duration / 60;
        let beatCount = (durationMin * bpm)/2;
        let beatWidth = width*2/beatCount; // width of 1 beat in pixels

        // offset by startSteps % to 32

        let sps = SECONDS_PER_STEP(bpm);
        let modOffsetSeconds = (32 - (startStep % 32) + 8)*sps;

        let addiitional = (2*width)*(modOffsetSeconds / duration);

        if (startStep === 0) {
        } else {
            offset += addiitional;
        }

        // how much of steps to offset by


        let beats = [];
        for (let i=0; i < beatCount; i++) {
            beats.push(beatWidth*i + offset);
        }
        setBeats(beats);
        setOffset(offset);
        setBeatWidth(beatWidth);
    }, [buffer, start, silence, width, bpm, startStep, length]);

    const isInside = useCallback((i) => {
        return 100*(offset + i*beatWidth)/(2*width) >= start && 100*(offset+i*beatWidth)/(2*width) <= start + length;
    }, [offset, beatWidth, width, start, length]);
    return {beats, isInside};
}
