import ENSName from 'components/ens/ENSName.js';
import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import LoadingRemix from './LoadingRemix.js';
import Zorb from './Zorb.js';
import './currently-playing.scss';

const CurrentlyPlaying = ({loading, playing}) => { 
    let [fullscreen, setFullScreen] = useState(false);

    useEffect(() => {
        if (playing && !loading) {
            setFullScreen(true);
        }
    }, [playing, loading]);
    
    if (!playing) {
        return '';
    }
    let zorbId = playing && playing.metadata.attributes.find(x => x.trait_type === 'zorb').value;
    let genImg = playing.metadata.image;
    return (
        [
            fullscreen && <div className="fullscreen-backdrop"
                               onClick={() => setFullScreen(false)}
                          >
                            <img src={genImg} className="fullscreen-gen"/></div>,
        <div className="currently-playing">
          {loading && <LoadingRemix/>}
          {!loading && <img onClick={() => setFullScreen(true)} src={genImg} className="gen-img pushable"/>}
          {!loading && playing && !loading && <Zorb className="zorb" zorbId={zorbId}/>}
          {!loading && <ENSName address={playing.owner}/>}

        </div>
            ]
        );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(CurrentlyPlaying);
