import React, {useEffect, useCallback, useState} from 'react';
import {SECONDS_PER_STEP} from './getRuler.js';
import {computeStemsData} from 'stems/getStems.js';

// start and length are % values w.r.t the duration of the waveform
export const useCroppedStemsData = ({
    endTime,
    loopsData,
    stemsData,
    startStep,
    silence,
    start,
    length,
    buffer
}) => {
    let [croppedStemsData, setCroppedStemsData] = useState({});
    useEffect(() => {
        // based on the start step (of the entire buffer) 

        // so loopsData -> raw shit
        // stemsData is simply a mapping between ipfsHash -> stemId so we dont need to change

        // we must "truncate" loopsData to only apply to the "cropped" section specified
        // by start x length x buffer x start step

        let croppedSynths = cropData(loopsData.synths, start, length, buffer, endTime);
        let croppedDrums = cropData(loopsData.drums, start, length, buffer, endTime);
        //let croppedLoopsData = {synths: croppedSynths, drums: croppedDrums};
        let computed = computeStemsData(loopsData, stemsData); //croppedLoopsData,  stemsData);
        // todo: need to computeStemsData based on the "start-step" set by the cropper

        // these variables need to converted to stem id + contract address pairs
        setCroppedStemsData(computed);

    }, [silence, stemsData, start, length, setCroppedStemsData, endTime]);

    return {
        croppedStemsData
    };
};

const cropData = (loopsData, start, length, buffer, recordEndTime) => {
//    console.log("SILENCE = ", silence);
 //   console.log("crop data for start=%s length=%s", start, length);
    loopsData = loopsData.map(x => ([isNaN(x[0]) ? 0 : x[0], x[1], x[2]]));
    // first we must get the full duration in steps
    let duration = buffer[0].length/44100;
    let secondsPerStep = SECONDS_PER_STEP(90);

    let startTime = recordEndTime - duration*start/100;

    let endTime  = startTime+  duration*length/100;;
//    console.log("Steps. START=%s END=%s OG_START_STEP=%s", startTime, endTime, startStep);

    // find the field just before startIntSteps
    // find the field just before endInSteps

    let firstField = undefined;

    let prevField = loopsData[0][2];
    for (let [step, hash, time] of loopsData) {

        if (time >= startTime) {
            firstField = prevField || time;
            // look at pervious field we've found 
            break;
        }
        prevField = time;
    }

    let lastField = undefined;

    prevField = undefined;
    for (let [step, hash, time] of loopsData) {
        if (time >= endTime) {
            lastField = prevField || time;
 //           console.log("END TIME FOUND (%s) BECAUSE time=%s >= endTime=%s", lastField, time, endTime);
            break;
        }
        prevField = time;
    }

    if (lastField === undefined) {
        lastField = loopsData[loopsData.length - 1][2];
    }

  //  console.log("loopsData=", loopsData);
  //  console.log("first and last fields=", firstField, lastField);

    let x = loopsData.filter(x => x[2] >= firstField && x[2] <= lastField);
  //  console.log("RETURNING ", x);

    return x;
};
