import React, {useEffect, useState} from 'react';
import {web3} from 'decentralized/web3.js';
import {minter} from 'zpore/abi/zpore-minter.js';
import BackendQuery from 'decentralized/BackendQuery.js';
import {
    MintPriceUpdated,
    ZporeDropCreated} from 'decentralized/EthereumEvents.js';
import {STEMS_CONTRACT} from 'stems/contract.js';

let {deployed} = minter;

export const useZporeMintPrice = (songId) => {
    let [dropAddress, setDropAddress] = useState();
    let [mintPrice, setMintPrice] = useState();

    useEffect(() => {
        getDropAddress(songId).then(
            dropAddress => {
                setDropAddress(dropAddress);
                getMintPrice(dropAddress).then(setMintPrice);
            });
    }, [songId]);

    return {mintPrice, dropAddress};
};

const getMintPrice = async (dropAddress) => {
    let query = new BackendQuery(deployed.goerli)
        .from(MintPriceUpdated)
        .where('targetZoraDrop', dropAddress);
    ;
    let results = await query.executeQuery();
    let result = results[0];
    let mintPrice = price(result.newMintPrice.slice(3)); //web3.utils.toBN(result.newMintPrice);
    return mintPrice;
};

const getDropAddress = async (songId) => {
    let query = new BackendQuery(STEMS_CONTRACT)
        .from(ZporeDropCreated)
        .select("dropAddress")
        .select("songId")
        .where("songId", parseInt(songId));

    let results = await query.executeQuery();
    if (!results[0]) {
        // error
        return;
    }
    let dropAddress = results[0].dropAddress;
    return dropAddress;
};
 
export const price = (x) => parseInt(x, 16)/1000000000000000000;
