
export const attribute = (type, value) => {
    return {
        type: 'attribute',
        value: {
            type,
            value
        }
    };
};

export const walk = (s) => {
    return {
        type: 'walk',
        value: s
    };
};

// b1 is is startTEnsion
// b2 is end tension
export const curve = (a=5,b1=10,c=15,b2=20) => {
    return {
        depth: (x) => curve(a, x, c, b2),
        tension: (x) => curve(a, b1, c, x),
        start: (x) => curve(x, b1, c, b2),
        end: (x) => curve(a, b1, x, b2),
        mirror: () => curve(a, -b1, c, b2),
        type: 'smooth',
        value: [a,b1,c,b2]
    };
}

export const cut = (a,b,c,_factor=1, _walk) => {
    // should be functional first and foremost so it doesnt mutate ever
    const factor = (x) => {
        return cut(a,b,c,x,_walk);
    };

    const walkFn = (x) => {
        return cut(a,b,c,_factor,x);
    };

    let curve1 = curve(a,b,c);
    let curve2 = curve(a,-_factor*b,c);
    let operations = _walk ? [curve1, walk(_walk), curve2] :
        [curve1, curve2];
    console.log('ops=', operations);
    return {
        factor,
        walk: walkFn,
        operations
    };
}

export const relative = (v) => {
    return attribute("relative", v);
}

export const radius = (v) => {
    return attribute("radius", v);
};

