import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './progress.scss';

const CircularProgressBar = ({percentage, radius}) => { 
    let strokeWidth = 6;
    let circum = 2*(radius-2*strokeWidth)*Math.PI;
    let a = percentage/100*circum;
    let b = (100-percentage)/100*circum;
    let style = {
    };
    let arr = [];
    for (let i=0; i < 16; i++) {
        arr.push((percentage/32)/100*circum);
        arr.push((percentage/32)/100*circum);
    }
    arr.pop();
    arr.push(b);
    return (
        <svg className="circular-progress" width={2*radius+'px'} height={2*radius+'px'}>
          <defs>
            <linearGradient id="linear" x1="0%" y1="0%" x2="100%" y2="0%">
              <stop offset="0%" stop-color="#aaf94c"/>
              <stop offset="50%" stop-color="#caf9ac"/>
              <stop offset="100%" stop-color="#0af90c"/>
            </linearGradient>
          </defs>
          <circle
            className="outter-circle"
            strokeWidth={strokeWidth}
            style={style} cx={radius+strokeWidth} cy={radius+strokeWidth} r={radius-strokeWidth*2} />
        {percentage > 0 && <circle
            className="inner-circle"
            strokeDashoffset={(radius+strokeWidth/4)}
            strokeWidth={strokeWidth}
            strokeDasharray={arr.join(',')}
           stroke="url(#linear)"
                             style={style} cx={radius+strokeWidth} cy={radius+strokeWidth} r={radius-strokeWidth*2} />}
       </svg>);

}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(CircularProgressBar);
