import React, { useRef, useEffect, useCallback, useState} from 'react';
import ToggleSwitch from './ToggleSwitch.js';
import {decodeAudioData} from 'utils/decodeAudioData.js';
import ipfs from "decentralized/Ipfs.js";
import {connect} from 'react-redux';
import StemLoop from './StemLoop.js';
import AudioUnit from 'engine/audio.js';

const SongStems = ({song, stems, setAddingMoreStems, isAdding, hasEdits, setHasEdits, setStems}) => { 
    let audioUnit = useRef(new AudioUnit());
    let playing = useRef();
    let [currentlyPlaying, setCurrentlyPlaying] = useState(undefined);

    const onDefaultChanged = useCallback((stem, isDefaultStem) => {
        stem.isDefaultStem = isDefaultStem;
        song.stems = [... stems];
        setHasEdits(hasEdits+1);
    }, [stems, song, hasEdits]);

    const play = useCallback(async (stem) => {
        if (playing.current === stem) {
            audioUnit.current.stopSound(0);
            setCurrentlyPlaying(undefined);
            return;
        }
        setCurrentlyPlaying(stem);
        console.log("IPFS=", stem.uri);
        let ipfsHash = stem.uri.slice("/ipfs/".length);
        let blob = await ipfs.getSound(ipfsHash);
        let buffer = await decodeAudioData(blob);
        audioUnit.current.setBuffer(buffer);
        audioUnit.current.playSound(0);
        playing.current = stem;
    }, [setCurrentlyPlaying]);

    return (
        <div className="song-stems">
          <div
            onClick={() =>    window.open(
                "https://spores.vision/zp/" + song.songId, "_blank")}
            className="pushable open-in-spores">
            Open in Spores
          </div>
        {stems.map(x =>
            <div
              onClick={() => play(x)}
              className={x.isLoop ? "song-stem" : "song-stem is-not-loop"}>
              <div className="stem-id">
                Stem #{x.stemId}
              </div>
              <div className="stem-types">
                {x.prettyStemTypes.map(x => <div className="selected stem-type">{x}</div>)}

              </div>
              <StemLoop sample={x} isEditable={false}/>
              <ToggleSwitch
                name="Is Default"
                value={x.isDefaultStem} onChange={(value) => onDefaultChanged(x, value)} isEditable={true}/>
              {currentlyPlaying === x && <img src="/speaker-playing.svg"/>}
            </div>)}
        {!isAdding && <div
            onClick={() => setAddingMoreStems(true)}
            className="add-stems pushable">
            add more stems to this song
                      </div>}
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SongStems);
