import BackendQuery from 'decentralized/BackendQuery.js';
import {ethers} from 'ethers';
import {ERC721Transfer} from 'decentralized/EthereumEvents.js';
import {web3} from 'decentralized/web3.js';
import {DROP_CONTRACT} from './zpore.js';
import {creator} from './abi/zpore-drop-creator.js';
import {ERC721} from './abi/erc721.js';
import {getDropAddress} from './getDropAddress.js';
import { ZDK, ZDKNetwork, ZDKChain } from "@zoralabs/zdk";

const networkInfo = {
    network: ZDKNetwork.Ethereum,
    chain: ZDKChain.Goerli,
};

const API_ENDPOINT = "https://api.zora.co/graphql";
const args = { 
              endPoint:API_ENDPOINT, 
              networks:[networkInfo], 
              //apiKey: process.env.API_KEY 
};
const zdk = new ZDK(args) // All arguments are optional

export const getZporeRemixes = async (songId) => {
    console.log(ZDKChain);
    let dropAddress = await getDropAddress(songId);
    console.log("DROP ADDRESS FOR token=%s ->", songId, dropAddress);
    console.log("args = ", args);
    
    let results = await zdk.tokens({
        where: {
            collectionAddresses: [dropAddress],
        },
        includeFullDetails: true});
    let tokens = results.tokens.nodes.map(x => x.token);

    return tokens.map(
        x => {
            if (!x.metadata) {
                x.metadata = decode(x.tokenUrl);
            }
            return x;
        }).filter(x => x.metadata);
};


export const getTokenURI = async (tokenId, address) => {
    const contract = new web3.eth.Contract(ERC721.abi, address);

    const result = await contract.methods.tokenURI(tokenId).call();
    return decode(result);
}

const decode = (uri) => {
    let starter = 'base64,';
    if (uri.indexOf(starter) >= 0) {
        let str = atob(uri.slice(uri.indexOf(starter) + starter.length));
        return JSON.parse(str);
    }
    return "";
};
