import React, { useEffect, useCallback, useState} from 'react';
import {getSegmentedPath, getPath, getPathString} from './getPath.js';
import {connect} from 'react-redux';
import './roots.scss';
const SporeRoots = ({refs, rootRef}) => { 
    if (!rootRef || !rootRef.current) {
        return;
    }
    return (<svg className="spore-roots">
            {refs.map(
                (ref,i) => <SporeBranch refs={refs} to={ref} from={rootRef} i={i}/>)}
            </svg>);
};

const SporeBranch = ({to, from, refs, i}) => {
    let [positions, setPositions] = useState();

    useEffect(() => {
        let toRect = to.current.getBoundingClientRect();
        let fromRect = from.current.getBoundingClientRect();
        setPositions([fromRect.left+0.5*fromRect.width+0.5*(toRect.left-fromRect.left)/600*fromRect.width, fromRect.top+fromRect.height, toRect.left+25, toRect.top]);
    }, [refs, i]);

    if (!positions) {
        return [];
    }
    let [x1,y1,x2,y2] = positions;
    let path = getPath(x1,y1,x2,y2);
    let d = getPathString(path);
    return [
        <path d={d} className="not-dashed"/>,
        <path d={d} className="dashed"/>,
    ];
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SporeRoots);
