export const addToPinata = (buffer, isBlob) => {
    return new Promise((resolve) => {
        var data = new FormData();
        if (isBlob) {
            data.append("file",buffer);
        } else {
            data.append("file",new Blob([buffer]));
        }
        var headers= { 
                pinata_api_key: '66a8464d59553776562c',
                pinata_secret_api_key: '96b4e32f40381e7e062569b89530f28051e00b488ec88bf3aa21c920be109109',
            };
        
        let url= 'https://api.pinata.cloud/pinning/pinFileToIPFS';
        fetch(url, {
            method: 'POST',
            headers,
            body: data
        }).then(async (resp) => {
            let json = await resp.json();
            resolve(json.IpfsHash);
        });
    });
};

