import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';


const SavingProgressInfo = ({
    download,
    transaction,
    downloading,
    isWaitingForSignature
}) => { 
    if (downloading) {
        return (
            <div className="download-test">
              <UnknownProgressBar/>
            </div> 
        );
    } else if (isWaitingForSignature) {
        return (
            <div className="waiting download-test">
              Open your connected wallet and sign the message to transmit the remix on-chain.
              <div className="test-net">
                This is on a test-network, as we develop this.
              </div>
              <UnknownProgressBar/>
            </div> 
        );
    } else {
        return (<div className="download-test">
         Download your remix <a target="_blank" href={"https://zequencer.mypinata.cloud/ipfs/" + download}>here</a>. We're still working on some things :)
              <br/>

                    <a href={"https://ropsten.etherscan.io/tx/" + transaction}>Etherscan ↗</a>
                </div>);
    }
  return (<div></div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SavingProgressInfo);
