import BackendQuery from "decentralized/BackendQuery.js";
import {TYPE_TO_NAME} from "constants/stem-types.js";
import {
    NewSong,
    NewStem,
    NewDefaultSongConfiguration,
    SongConfigurationStem,
    NewStemType} from 'decentralized/EthereumEvents.js';
import StemsContract from "decentralized/contracts/StemsContract.js";
let contract = new StemsContract().contractAddress;

export const getSongs = async (songIds) => {
    let query = new BackendQuery(contract)
        .from(NewSong);

    if (songIds) {
        query.whereIn("songId", songIds);
    }
    let songs = await query.executeQuery();
    console.log("SONGS RETURNED=", songs);
    let defaultStems = await getDefaultStems(songs.map(x => x.songId));
    for (let song of songs) {
        // grab stems
        let songId = song.songId;
        let query = new BackendQuery(contract)
            .from(NewStem)
            .where('songId', songId);
        let stems = await query.executeQuery(true,true);
        console.log("STEMS for songId=", songId,  stems.map(x => x.uri));
        let stemIds = stems.map(x => x.stemId);
        let typesQuery = new BackendQuery(contract)
            .from(NewStemType)
            .whereIn('stemId', stemIds);

        let songDefaultStems = defaultStems[songId];
        let types = await typesQuery.executeQuery(true,true);
        for (let stem of stems) {
            if (songDefaultStems) {
                stem.isDefaultStem = songDefaultStems.includes(stem.stemId);
            } else {
                stem.isDefaultStem = false;
            }
            let _types = types.filter(x => x.stemId === stem.stemId);
            stem.stemTypes = _types.map(x => x.stemType);
            stem.prettyStemTypes = _types.map(x => TYPE_TO_NAME[x.stemType]);
        }
        song.stems = stems;
    }
    return songs;
};

const getDefaultStems = async () => {
    let query = new BackendQuery(contract)
        .from(NewDefaultSongConfiguration);

    let results = await query.executeQuery(true,true);

    let songIds = new Set();
    let configIdToSongId = {};
    for (let result of results) {
        // we want the latest configs, they are orded by newest -- so
        // skip if exists
        if (!songIds.has(result.songId)) {
            configIdToSongId[result.configId] = result.songId;
        }
        songIds.add(result.songId);
    }

    // now we got the configs so lets grab the stems per config
    let configIds = Object.keys(configIdToSongId);
    configIds = configIds.map(x => parseInt(x));
    let stemsQuery = new BackendQuery(contract)
        .from(SongConfigurationStem)
        .whereIn('configId', configIds);

    let stemResults = await stemsQuery.executeQuery(true,true);

    let songToStems = {};
    for (let result of stemResults) {
        let songId = configIdToSongId[result.configId];
        if (!songToStems[songId]) {
            songToStems[songId] = [];
        }
        songToStems[songId].push(result.stemId);
    }

    return songToStems;
};


export const getAllStems = async () => {
    let query = new BackendQuery(contract)
        .from(NewStem);

    let results = await query.executeQuery(true,true);

    console.log("RESULTS WE GOT=", results);
    let allStems = results.map(x => x.uri.slice("ipfs://".length));
    console.log("ALL STEMS gotten=", allStems);
    return allStems;
};
