import React, { useRef, useLayoutEffect, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {peval} from './eval.js';
import {walk, curve, relative, cut, radius} from './dsl.js';
import './curve.scss';

const CurveDiv = ({
    children,
    fill="#0000008c",
    stroke="white",
    className="",
    algo=[],
    relative=true,
    radius=60
}) => { 
    let ref = useRef();

    const [size, setSize] = useState({width: 0, height: 0});
    const onChange = useCallback(() => {
        if (!ref.current) {
            return;
        }
        let _size = {
            width: ref.current.offsetWidth,
            height: ref.current.offsetHeight
        };
        setSize(_size);
    }, [setSize]);

    useEffect(() => {
        new ResizeObserver(onChange).observe(ref.current)
    }, []);

    let width = Math.max(20, size.width-radius*2);
    let height = Math.max(20, size.height-radius*2);

    let {d, xIncrease, yIncrease} = peval({
        width,
        height,
        algo,
        radius,
        relative
    });
    // we need to pass
    // ([go(50), smooth(800, 50, 140)], [go(100), smooth(400, 30, 100)], [go(150), smooth(400, 30, 100)]

    // what kind of scriping language can I do for this
    // a DSL that can be passed into the props of here and executed

    //d += "150,0 150, 0"
    //d += "0, 150 150, -150";
    return (<div
              style={{blockSize: "fit-content"}}
              ref={ref} className={"relative flex flex-col " + className}>
              <svg
                width={xIncrease + size.width + 100 + 'px'}
                height={yIncrease + size.height + 100 + 'px'}
                className="absolute top-0 left-0 z-0">
                <path stroke={stroke} fill={fill}
                      d={d} />
              </svg>
              {children}
            </div>);
}

export default CurveDiv;

