import React, { useRef, useEffect, useCallback, useState} from 'react';
import Circular from 'components/progressbar/Circular.js';
import {connect} from 'react-redux';
import {useInterval} from 'components/hooks/useInterval.js';

const ZporeLoading = ({setFinished, playerRef}) => { 
    let [percentage, setPercentage] = useState(0);
    let interval = useRef([]);
    let progress = useRef(0);
    let shownProgress = useRef(0);
    let velocity = useRef(1);
    
    useEffect(() => {
        window.addEventListener("message", onMessage);
        return () => window.removeEventListener("message", onMessage);
    }, [setPercentage, percentage, setFinished, playerRef]);


    const onTick = useCallback(() => {
        if (shownProgress.current > 94) {
            velocity.current *= 0.99;
        }
        let x = shownProgress.current+velocity.current*0.1;
        setPercentage(x);
        shownProgress.current = x;
    }, [setPercentage]);

    useInterval(onTick, 20);

    const onMessage = useCallback((e) => {
        if (e.data.type === "loading-progress") {
            velocity.current = Math.min(7, Math.max(4.5, (e.data.body - progress.current)/10));
            progress.current = e.data.body;
            if (shownProgress.current > progress.current) {
                velocity.current *= 0.5;
            }
        }
        if (e.data === "Finished Loading Project") {
            if (playerRef) {
                console.log("acknowledging");
                playerRef.current.contentWindow.postMessage({
                    "type": "finished-acknowledged",
                    body: true
                }, "*");
            }
            setFinished(true);
        }
    }, [setPercentage, setFinished, percentage, playerRef]);
    
    return (
        <Circular percentage={percentage} radius={130}/>
        );
};

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZporeLoading);
