import React, {useState, useEffect, useCallback} from 'react';
import {addToPinata} from 'utils/addToPinata.js';
import Tone from 'tone';
import ipfs from 'decentralized/Ipfs.js';
import Sample from 'repos/Sample.js';
import {decodeMP3} from 'mp3/mp3.js';

export const useAudioDrop = () => {
    let [samples, setSamples] = useState([]);
    let [isUploading, setIsUploading] = useState(false);
    const onDrop = useCallback(async (ev) => {
        setIsUploading(true);
        ev.stopPropagation();
        ev.preventDefault();
        ev.dataTransfer.effectAllowed = "none";
        ev.dataTransfer.dropEffect = "none";
        
        if (ev.dataTransfer.items) {
            let promises = [];
            let len = ev.dataTransfer.items.length;
            for (var i = 0; i < len; i++) {
                // If dropped items aren't files, reject them
                if (ev.dataTransfer.items[i].kind === 'file') {
                    promises.push(uploadFile(
                        ev.dataTransfer.items[i].getAsFile()));
                }
            }
            let samples = await Promise.all(promises);
            setSamples(samples.filter(x => x.audioBuffer));
        }
        setIsUploading(false);
    }, [setSamples]);

    return {onDrop, samples, isUploading};
};

export const uploadFile = (file) => {
    return new Promise((resolve) => {
        let reader = new FileReader();
        if (!file.name.includes('.wav') && !file.name.includes('.flac') && !file.name.includes('.mp3')) {
            window.alert('Only accepts .wav, .mp3, & .flac files for now');
            return;
        }
        reader.readAsArrayBuffer(file)
        reader.onloadend = async () => {
            let raw = reader.result;
            let buffer = new Buffer(raw);
            
            let savedRaw = Int8Array.from(raw);
            let ipfsHash = await addToPinata(buffer); //ipfs.addArrayBuffer(raw);
            let audioBuffer;
            let sample = new Sample(ipfsHash);
            if (file.name.includes('mp3')) {
                audioBuffer = await decodeMP3(new Int8Array(raw));
                sample.mp3Data = new Buffer(raw);
                
            } else {
                audioBuffer = await Tone.Master.context.decodeAudioData(raw);
            }
            
            sample.audioBuffer = audioBuffer;
            sample.name = file.name;
            resolve(sample);
        }
    });
};

