import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {getZorb} from 'utils/getZorb.js';

const Zorb = ({zorbId, ...props}) => { 
    let [zorb, setZorb] = useState(undefined);
    useEffect(() => {
        if (zorbId !== undefined) {
            getZorb(zorbId).then(setZorb);
        }
    }, [setZorb, zorbId]);

    if (!zorb) {
        return '';
    }
    return (<img {... props} src={zorb}/>);
}

export default Zorb;
