import React, { useEffect, useCallback, useState} from 'react';
import { ConnectButton, darkTheme } from '@rainbow-me/rainbowkit';
import SongStems from './SongStems.js';
import SaveFlow from './SaveFlow.js';
import {useFlags} from 'flags/flags.js';
import StemOption from './StemOption.js';
import UnknownProgressBar from "components/progressbar/UnknownProgressBar.js";
import {connect} from 'react-redux';
import {useAudioDrop} from "components/hooks/useAudioDrop.js";
import {getSongs} from 'stems/getStemz.js';
import './stems.scss';

const Stems = (props) => { 
    const [addingToSong, setAddingToSong] = useState(undefined);
    const [completed, setCompleted] = useState(false);
    const [songs, setSongs] = useState([]);
    const [stems, setStems] = useState([]);
    const {isUploading, samples, onDrop} = useAudioDrop();
    const [song, setSong] = useState(undefined);
    const [hasEdits, setHasEdits] = useState(0);

    useFlags();

    useEffect(() => {
        getSongs().then(setSongs);
    }, []);

    useEffect(() => {
        getSongs().then(setSongs);
        setStems([]);
    }, [completed]);

    useEffect(() => {
        setStems(samples);
    }, [samples, setStems]);

    useEffect(() => {
        window.addEventListener("dragenter", function(e) {
            e.preventDefault();
            e.dataTransfer.effectAllowed = "none";
            e.dataTransfer.dropEffect = "none";
        }, false);
        
        window.addEventListener("dragover", function(e) {
            e.preventDefault();
            e.dataTransfer.effectAllowed = "none";
            //e.dataTransfer.dropEffect = "none";
            return false;
        });
        window.addEventListener("drop", onDrop);
        
    }, []);

    const deleteSample = useCallback((x) => {
        setStems(stems.filter(y => y !== x));
    }, [stems, setStems]);

    return (
        <div
          onDrop={onDrop} 
          onDragOver={e => e.preventDefault()}
          className="stems-portal">
          <div className="layout">
            <div className="connect-button-wrapper">
              <ConnectButton
                accountStatus="address" 
                showBalance={{ smallScreen: false, largeScreen: false }}
                label="Connect Wallet"
              />
            </div>
            <div className="header">STEMS SPORES</div>
            <div className="f-row">
               <div className="drop-files-here">
                 <span>
                   {addingToSong ? "Drop stems for " + addingToSong.title : "Drop Files Here"}
                 </span>
               </div>
              <div className="songs">
                {songs.map(_song => <div
                                      onClick={() => _song === song ? setSong(undefined) : setSong(_song)}
                                      className={song && song.songId === _song.songId ? "song selected" : "song"}>
                                      <div className="song-title">Song #{_song.songId}</div>
                                      <div className="song-title">{_song.title}</div>
                                      <div className="song-artist">{_song.artist}</div>
                                      <div className="song-bpm">{_song.bpm}</div>
                                      <div className="song-key">{_song.key}</div>
                                   </div>)}
              </div>
              {song &&
               <div className="song-info">
                 <SongStems
                   song={song}
                   setHasEdits={setHasEdits}
                   hasEdits={hasEdits}
                   isAdding={addingToSong}
                   setAddingMoreStems={() => setAddingToSong(song)}
                   stems={song.stems}/>
               </div>}
            </div>
            <div className="samples">
              {stems.map(x => <StemOption sample={x} deleteSample={deleteSample}/>)}
              {isUploading && <UnknownProgressBar/>}
            </div>
            <SaveFlow
              completed={completed}
              setCompleted={setCompleted}
              hasEdits={hasEdits} openedSong={song} song={addingToSong} hideFields={addingToSong} stems={stems}/>
        {completed &&
         <div className="completed-info">
           Succesfully added to the Spores Library
           <div
             onClick={() => setCompleted(false)}
             className="close-btn">
           x
         </div>
         </div>}
          </div>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Stems);
