import React, { useEffect, useCallback, useState} from 'react';
import {formatClock} from 'utils/formatDate.js';
import {connect} from 'react-redux';

const Clock = (props) => { 
    let [clock, setClock] = useState({
        duration: 0,
        progress: 0,
    });
    useEffect(() => {
        window.addEventListener("message", onMessage);
        return () => window.removeEventListener("message", onMessage);
    }, [setClock, clock]);
    const onMessage = useCallback((e) => {
        if (e.data.type === "song-position") {
            setClock({
                ... clock,
                progress: e.data.body
            });
        }
        if (e.data.type === "song-duration") {
            setClock({
                ... clock,
                duration: e.data.body
            });
        }
    }, [setClock, clock]);
    return (<div className="text-sm">
              {formatClock(clock.progress/100*clock.duration)} / {formatClock(clock.duration)}
            </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Clock);
