import SavingProgressInfo from './SavingProgressInfo.js';
import {ethers} from 'ethers';
import {useZporeMintPrice} from './useZporeMintPrice.js';
import {useCroppedStemsData} from './useCroppedStemsData.js';
import {newZpore} from 'zpore/zpore.js';
//import TextHelper from './TextHelper.js';
import {writeScreenshot} from './screenshot.js';
import {usePostRemix} from './usePostRemix.js';
import SlicingTooltip from './SlicingTooltip.js';
import {REMIX_COLLECTION_ADDRESS, REMIX_TOKEN_ID} from 'constants/zpore-addresses.js';
import {useMagneticSlicer} from './useMagneticSlicer.js';
import React, { useEffect, useCallback, useRef, useState} from 'react';
import ipfs from 'decentralized/Ipfs.js';
import {cropAndStore} from 'utils/cropAndStore.js';
import {newSpore} from 'spores/newSpore.js';
import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';
import { useSigner } from 'wagmi';
import {isMobile} from 'react-device-detect';
import {useWaveformPlayer} from './useWaveformPlayer.js';
import PlayButton from 'components/buttons/PlayButton.js';
import {connect} from 'react-redux';
import SliceWaveformSVG from './SliceWaveformSVG.js';
import Captioner from './Captioner.js';
import './slice-waveform.scss';

const SliceWaveform = ({
    silence,
    songId,
    endTime,
    screenshot,
    zorbId,
    beatGuide,
    waveform,
    buffer,
    onCancel,
    startStep,
    stemsData, // these 2 should be 1...
    loopsData, 
}) => { 
    let ref = useRef();
    const {data: signer} = useSigner();
    let [caption, setCaption] = useState('');
    let [isWaitingForSignature, setIsWaitingForSignature] = useState();
    let [downloading, setDownloading] = useState(false);
    let [isCaptioning, setIsCaptioning] = useState(false);
    let [width, setWidth] = useState();
    let [download, setDownload] = useState();
    let [transaction, setTransaction] = useState();
    let [bpm, setBPM] = useState(120);
    let [detune, setDetune] = useState(0);


    useEffect(() => {
        // calculates the width that should be set for the waveform
        let _width = isMobile ? 300 : ref.current.offsetWidth;
        let width = (_width - 60)/2;
        setWidth(width);
    }, [setWidth]);

    const {
        start,
        length,
        dragEndControl,
        dragStartControl
    } = useMagneticSlicer({ref, width, buffer, startStep, silence, waveform, endTime, beatGuide, endTime});

    const {
        croppedStemsData
    } = useCroppedStemsData({stemsData, loopsData, start, length, startStep, silence, buffer, endTime});

    let {dropAddress, mintPrice} = useZporeMintPrice(songId);

    const {
        playhead,
        stop,
        play,
        isPlaying
    } = useWaveformPlayer({buffer, start, length});

    // todo: combine this data into one esy to use object...
    const post = usePostRemix({
        bpm,
        detune,
        mintPrice,
        stemsData: croppedStemsData,
        screenshot,
        songId,
        caption,
        buffer,
        signer,
        start,
        setIsWaitingForSignature,
        setDownloading,
        length,
        setDownload,
        stop,
        setTransaction,
        zorbId
    });

    if (transaction || downloading || isWaitingForSignature) {
        return (
            <div className="slicer-backdrop">
              <SavingProgressInfo
                download={download}
                transaction={transaction}
                downloading={downloading}
                transaction={transaction}
                isWaitingForSignature={isWaitingForSignature}/>
            </div>
        );
    }
    return (
        <div className="slicer-backdrop">
          <div
            className="slice-waveform">
            <div className="control-btn-container">
              <PlayButton isPlaying={isPlaying} play={play}/>
            </div>
            <div
              ref={ref} 
              className="waveform-container">
              {isCaptioning ?
               <Captioner
                 setCaption={setCaption}
                 caption={caption}
               /> :
               width &&
               <SliceWaveformSVG
                 endTime={endTime}
                 beatGuide={beatGuide}
                 silence={silence}
                 height={isMobile ? 90 : 100}
                 buffer={buffer}
                 startStep={startStep}
                 start={start} length={length} waveform={waveform} width={width}/>
              }
              {!isCaptioning &&
               <div
                 style={{left: 30 + (playhead/100)*(width)*2+'px'}}
                 className="playhead"/>
              }
              {!isCaptioning &&
               <div
                 style={{left: start/100*(width*2)+'px', width: 60+length/100*(width*2)+"px"}}
                 className="slicer">
                 <div
                   onMouseDown={dragStartControl}
                   onTouchStart={dragStartControl}
                   className="start-control">
                   <SlicingTooltip
                     bpm={bpm}
                     setBPM={setBPM}
                     detune={detune}
                     setDetune={setDetune}
                     beatGuide={beatGuide} endTime={endTime} start={start} buffer={buffer}/>
                   <div className="arrow-left"/>
                 </div>
                 <div
                   onTouchStart={dragEndControl}
                   onMouseDown={dragEndControl}
                   className="end-control">
                   <div className="arrow-right"/>
                 </div>
               </div>}
            </div>
             <div className="slice-guide-info">
               Slice your remix to mint your favorite moment.
             </div>
          </div>

          {downloading ? '' : download ?
           <div className="slice-toolbar">
             <div onClick={onCancel} className="done-btn pushable continue">
               Continue
             </div>
           </div> :
           <div className={isCaptioning ? "slice-toolbar captioning" : "slice-toolbar"}>
             <div onClick={isCaptioning ? () => setIsCaptioning(false) : onCancel} className="cancel-btn pushable">
               {isCaptioning ? "Edit" : "Cancel"}
             </div>
             <div onClick={() => {
                 post();
             }} className="done-btn pushable">
               mint {mintPrice} ETH
             </div>
           </div>}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SliceWaveform);
