export const getSegmentedPath = (x1, y1, x2, y2) => {
    if (y2 > y1) {
        return [
            x1, y1,

            x1, (0.75*y1 + 0.25*y2),
            x1, (0.75*y1 + 0.25*y2 + 1),

            x1, (0.5*y1 + 0.5*y2),
            x1, (0.5*y1 + 0.5*y2 + 1),
            
            x2, (0.5*y1 + 0.5*y2),
            x2, (0.5*y1 + 0.5*y2 + 1),

            x2, (0.25*y1 + 0.75*y2 + 0),
            x2, (0.25*y1 + 0.75*y2 + 1),

            x2, y2-2,
            x2, y2];
    }  else {
        // dont wanna interect the node
        return [
            x1, y1,

            x1, y1 + 15,
            x1, y1 + 16,

            x1, y1 + 30,
            x1, y1 + 31,

            x1*0.5 + x2*0.5, y1 + 30,
            x1*0.5 + x2*0.5, y1 + 31,

            x1*0.5 + x2*0.5, (.5*y1 + .5*y2) + 30,
            x1*0.5 + x2*0.5, (.5*y1 + .5*y2) + 31,

            x1*0.5 + x2*0.5, y2 - 30,
            x1*0.5 + x2*0.5, y2 - 31,

            x2, y2 - 30,
            x2, y2 - 31,

            x2, y2 - 16,
            x2, y2 - 15,

            x2, y2 - 2,
            x2, y2];
    }
};

export const getPath = (x1,y1,x2,y2, isHorizontal) => {
    let y_a = 40;
    let y_b = 100;
    if (y2 < y1) {
        y_a = 80;
    }

    if (isHorizontal) {
        return [
            x1, y1,
            x1, y1 + y_a,
            x2 - y_b, y2,
            x2, y2,
        ];
    } 

    return [
        x1, y1,
        x1, y1 + y_a,
        x2, y2 - y_b,
        x2, y2];
};

const fl = (x) => Math.floor(x);

export const getPathString = (points) => {
    let d = "M " + fl(points[0]) + ' ' + fl(points[1]);
    let pts = "";
    for (let i=2 ; i < points.length - 1; i+=2) {
        pts += (fl(points[i]) + ' ' + fl(points[i+1])) + ' ';
    }
    let command = "C";
    let u = d + " " + command + " " + pts;
    return u;
};
