export const getBeatPoints = (beatGuide, buffer, width, endTime, silence=0) => {
    let duration = buffer[0].length/44100;

    // what we really need is the end time to tie all of this together
    beatGuide = [... beatGuide].reverse();
    let pts = [];
    for (let [stepNumber, time] of beatGuide ) {
        if (stepNumber % 16 !== 0) {
            continue;
        }
        let fromEnd = endTime - time
        let x_offset = (fromEnd / duration) * width;
        // note whether this is a "bar start" or "middle of bar" tick
        pts.push([width - x_offset, stepNumber % 32 === 0 ? true : false]);
    }
    return pts;
};
