import React, { useEffect, useCallback, useState} from 'react';
import {triggerRefresh} from 'backend/cacheServer.js';
import {ethers} from 'ethers';
import UnknownProgressBar from "components/progressbar/UnknownProgressBar.js";
import {connect} from 'react-redux';
import ZoraField from "components/fields/ZoraField.js"
import SongKey from './SongKey.js';
import {useSigner} from 'wagmi';
import StemsContract from "decentralized/contracts/StemsContract.js";

export const STEMS_CONTRACT = "0x74Bf6d7caAB56b3E47c235f42401F27736Df9dFC";;

const SaveFlow = ({setCompleted, completed, hasEdits, stems, song, hideFields, openedSong}) => { 
    const {data: signer} = useSigner();
    let [syncing, setSyncing] = useState(false);
    let [title, setTitle] = useState('');
    let [artist, setArtist] = useState('');
    let [bpm, setBPM] = useState(90);
    let [key, setKey] = useState("B Minor");

    let disabled = stems.some(x => x.stemTypes.length === 0) || title === "" || stems.length === 0 || artist === '';
    if (hideFields) {
        disabled = stems.length === 0;
    }

    useEffect(() => {
        if (completed) {
            setTitle("");
            setArtist("");
        }
    }, [completed]);

    const save = useCallback(async () => {
        if (disabled) {
            return;
        }


        let uris = [];
        let stemTypes = [];
        let durations = [];
        let isLoops = [];
        const baseURL = "https://zequencer.mypinata.cloud/ipfs/";
        for (let stem of stems) {
            stemTypes.push(stem.stemTypes);
            uris.push(baseURL + stem.ipfsHash);
            durations.push(parseInt(stem.audioBuffer.duration)); // needs to be in seconds
            isLoops.push(stem.isLoop);
        }
        console.log("bpm=%s key=%s uris stemTypes", bpm, key, uris, stemTypes, durations, isLoops);

        let _contract = new StemsContract();
        const contract = new ethers.Contract(
            _contract.contractAddress, _contract.abi, signer);
        if (song) {
            let args=  [
                ethers.BigNumber.from(song.songId),
                uris,
                stemTypes,
                durations,
                isLoops,
            ];


            let tx = await contract.newStems(...args, { value: 0});
            
            let transactionHash = tx.hash;
            setSyncing(true);
            triggerRefresh(transactionHash, STEMS_CONTRACT);
            await tx.wait(5);
            setSyncing(false);
            setCompleted(true);
            return;
        }
        let args=  [title, artist, uris, stemTypes, isLoops, durations, parseInt(bpm), 0, key];
        console.log("args to send=", args);
        let tx = await contract.newSong(...args, { value: 0});
        setSyncing(true);
        let transactionHash = tx.hash;
        triggerRefresh(transactionHash, STEMS_CONTRACT);
        await tx.wait(3);
        setSyncing(false);
        setCompleted(true);
    }, [stems, title, artist, bpm, key, disabled, song, signer, setCompleted, setSyncing]);

    const saveNewConfiguration = useCallback((e) => {
        let defaultStems = openedSong.stems.filter(x => x.isDefaultStem).map(x => x.stemId);
        let songId =ethers.BigNumber.from(openedSong.songId);
        console.log('songId=', songId);
        let args=  [songId, defaultStems.map(ethers.BigNumber.from)];
        /*
        let transaction = new EthereumTransaction(
            STEMS, 'updateSongConfiguration', args
            );
            */
        setSyncing(true);
        // transaction.execute();
    }, [openedSong]);

    if (syncing) {
        return <UnknownProgressBar/>
    }

    if (hasEdits) {
        return (
            <div className="save-flow">
              <div
                onClick={saveNewConfiguration}
                className="save-configuration save-button pushable">
                Save New Default Configuration for Song
              </div>
            </div>
               );
    }

    return (
        <div className="save-flow">
          {!hideFields && <ZoraField get={title} set={setTitle} name="Title"/>}
          {!hideFields && <ZoraField get={artist} set={setArtist} name="Artist"/>}
          {!hideFields && <ZoraField get={bpm} set={setBPM} name="BPM"/>}
          {!hideFields && <SongKey get={key} set={setKey} name="Song Key"/>}
          <div
            onClick={save}
            className={disabled ? "save-button disabled" : "save-button pushable"}>
            Save
          </div>
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SaveFlow);
