import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {SECONDS_PER_STEP} from './getRuler.js';

const SlicingTooltip = ({endTime, beatGuide,  start, buffer, bpm, detune, setBPM, setDetune}) => { 
    useEffect(() => {
        let prevTime = -1;
        let bpm = 100;
        let detune = 0;
        start = (start / 100) * buffer[0].length / 44100;
        for (let i=0; i < beatGuide.length; i++) {
            let [stepNumber, time, _bpm, _detune] = beatGuide[i];
            let _time = (buffer[0].length / 44100) - (endTime - time);
            if (start >= prevTime && start < _time) {
                bpm = _bpm;
                detune = _detune;
                break;
            }
            prevTime = _time;
        }
        setDetune(detune);
        setBPM(bpm);
    }, [start, buffer, beatGuide, endTime, setBPM, setDetune]);
    return (
        <div className="start-info">
          <div className="flex-col w-8 flex">
            <span className="slice-len text-center">{Math.round(bpm)}</span>
            <span className="text-zinc-500 text-xs text-center">bpm</span>
          </div>
          <div className="flex-col w-8 flex">
            <span className="slice-len text-center">{detune > 0 ? '+' : ''}{round(detune/100)}</span>
            <span className="mx-1 text-zinc-500 text-xs text-center">tuning</span>
          </div>
          <div className="mt-0.5 ml-auto w-16 flex flex-col">
            <div className="text-xs flex w-full h-4">
              <div className="label mt-0.5">00:{round(start, true)}</div>
              <div className="text-xs ml-auto text-zinc-500">
                s
              </div>
            </div>
            <span className="mt-0.5 mx-1 text-zinc-500 text-xs text-center">start</span>
          </div>
        </div>
    );
};

const round = (x, isTime) => {
    x = Math.round(x*10)/10;
    if (isTime && x < 10) {
        return "0" + x;
    }
    return x;
};

export default SlicingTooltip;
