import React, { useEffect, useRef, useCallback, useState} from 'react';
import CurrentlyPlaying from './CurrentlyPlaying.js';
import {PLAYING} from 'constants/play-statuses.js';
import {togglePlayback} from 'player/controls.js';
import {useAudioPlayer} from 'components/hooks/useAudioPlayer.js';
import ipfs from 'decentralized/Ipfs.js';
import AudioUnit from 'engine/audio.js';
import {REMIX_COLLECTION_ADDRESS} from 'constants/zpore-addresses.js';
import {getZporeRemixes} from 'zpore/getZporeRemixes.js';
import {connect} from 'react-redux';
import ZorbRemixView from './ZorbRemixView.js';
import './remixes.scss';

const ZorbRemixes = ({remixes, songId, show, setShow, playerRef, playStatus}) => { 
    let [soundURL, setSoundURL] = useState();
    let {audioPlayer, loading} = useAudioPlayer(soundURL, true, true);
    let [playing, setPlaying] = useState();

    useEffect(() => {
        if (playing) {
            let sound = playing.metadata.animation_url;
            setSoundURL(sound);
        } else {
            setSoundURL(undefined);
        }
    }, [playing, audioPlayer, remixes, setSoundURL]);

    useEffect(() => {
        if (show && playStatus === PLAYING) {
            togglePlayback(playerRef.current);
        }
    }, [show, playStatus, playerRef]);

    if (show) {
        return [<ZorbRemixView
                  songId={songId}
                 audioPlayer={audioPlayer}
                 playing={playing}
                 setPlaying={setPlaying}
                 hide={() => {
                     if (audioPlayer) {
                         audioPlayer.stopSound();
                     }
                     setShow(false);
                 }} remixes={remixes}/>,
                <CurrentlyPlaying
                  loading={loading}
                  playing={playing}
                  audioPlayer={audioPlayer}/>];
    }
    return '';
    /*
    return (
        <div
          onClick={() => setShow(true)}
          className="remixes-button">
          <span>{remixes.length} Remixes 𓍊</span>
        </div>);
        */
};

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef,
      playStatus: state.playback.playStatus
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZorbRemixes);
