import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './waitlist.scss';

const Waitlist = (props) => { 
    let [submitted, setSubmitted] = useState(false);
    let [value, setValue] = useState('');

    const submit = useCallback(() => {
        setSubmitted(true);
    }, [setSubmitted]);

    return (
        <div className="waitlist flex h-screen  ">
          <div className="m-auto inner-waitlist">
            <img style={{width: "200px"}} className="mx-auto" src="/spore-logo-future4.png"/>
            <div className="info text-lg">
              What's your email?
            </div>
            <div className="info-next text-base mx">
              We'll send you an invite when the time is right.
            </div>
            <input className="w-full email-input mt-6 outline-0 text-base text-white bg-transparent" value={value} placeholder="Enter your email" onChange={e => setValue(e.target.value)}/>
            {submitted &&
             <div className="submitted-text">
               You've been added to the waitlist!
               <svg height="13" width="16"><path d="M14.293.293l1.414 1.414L5 12.414.293 7.707l1.414-1.414L5 9.586z"></path></svg>
             </div>}
             <div
               onClick={submit}
               className= {!submitted && value !== "" ? "ok-btn pushable show" : "ok-btn pushable"}>
            
               OK <svg height="13" width="16"><path d="M14.293.293l1.414 1.414L5 12.414.293 7.707l1.414-1.414L5 9.586z"></path></svg>
             </div>
          </div>
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Waitlist);
