export const triggerRefresh = (transactionHash, contractAddress) => {
    let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/" : "https://zequencer.io/";
    console.log("hostname=", window.location.hostname, baseURL);
    if (!window.LOCAL_BACKEND_FLAG) {
        baseURL = "https://zequencer.io/";
    }

    console.log('triggering refresh=', contractAddress, transactionHash, baseURL);
    fetch(baseURL + "refreshContract/", {
        method: "POST",
         headers: {
             'Accept': 'application/json',
             'Content-Type': 'application/json'
         },
        body: JSON.stringify({
            contractAddress,
            transactionHash
        })
    });

};
