import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import ToggleSwitch from './ToggleSwitch.js';

const StemLoop = ({sample, isEditable=true}) => { 
    let [isLoop, setIsLoop] = useState(sample.isLoop);

    const onChange = useCallback((value) => {
        setIsLoop(value);
        sample.isLoop = value;
    }, [isLoop, setIsLoop, sample]);
    return (<ToggleSwitch name="Loop" value={isLoop} onChange={onChange} isEditable={isEditable}/>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(StemLoop);
