import React, { useEffect, useCallback, useState} from 'react';
import Dropdown from 'react-dropdown'
import {connect} from 'react-redux';


let keys = ["C Minor", "C# Minor", "D Minor", "D# Minor", "E Minor", "F Minor", "F# Minor", "G Minor", "G# Minor", "A Minor", "A# Minor", "B Minor"];
let OPTIONS = keys.map(key => ({label: key, value: key}));
const SongKey = ({set, get}) => { 
    let className = "song-key zora-field focused";

    const onChange = useCallback((e) => {
        set(e.value);
    }, [set]);

    let value = OPTIONS.find(x => get === x.value);
    return (
        <div className={className}>
          <div className="field-name">
            Song Key
          </div>
          <Dropdown
            options={OPTIONS}
            onChange={onChange}
            value={value}
            placeholder="Key" />
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SongKey);
