

import Web3 from 'web3';
import {abi} from './zorb-abi.js';
const address = "0xCa21d4228cDCc68D4e23807E5e370C07577Dd152";

export const getZorb = (tokenId) => {
    return new Promise((resolve) => {
        let web3 = new Web3(new Web3.providers.HttpProvider('https://mainnet.infura.io/v3/2f3dce2af1044e88b4851c3d73c3d00c'));

        const contract = new web3.eth.Contract(abi, address);
        console.log("contract=", contract, abi, address, tokenId);
        const x = contract.methods.tokenURI(tokenId).call().then(
            uri => {
                let starter = 'data:application/json;base64,';
                if (uri.startsWith(starter)) {
                    let str = atob(uri.slice(starter.length));
                    let json = JSON.parse(str);
                    resolve(json.image);
                }
            });
    });
}
